// import dataLearn from '../public/learn.json'
import mindmap from './components/MindMap.vue'
// import { readXML } from './ts/utils'
@Component({ components: { mindmap } })
export default class App extends Vue {
  showSource = false
  onboarding = 0
  length = 3
  mmdata = this.readFile('https://cdn.jsdelivr.net/gh/WarringKingdoms/photo/img/%E8%B4%A2%E5%8A%A1%E8%87%AA%E7%94%B1%E4%B9%8B%E8%B7%AF.opml')
  items = [
    { title: 'template' },
    { title: 'script' },
  ]
  textColor = '#2627288A'
  backgroundcolor = '#03BFFFFF'
  @Watch('backgroundcolor')
  backgroundcolors(backgroundcolor: string) {
    console.log(backgroundcolor)
  }
  menu = false
  textmenu = false
  options = {
    gps: { value: true },
    fitView: { value: true },
    keyboard: { value: true },
    draggable: { value: true },
    showNodeAdd: { value: true },
    contextMenu: { value: true },
    zoomable: { value: true },
    showUndo: { value: true },
    download: { value: true },
    balanceLeaf: { value: true },
  }
  xSpacing = 50
  ySpacing = 20
  strokeWidth = 4
  readFile(filePath: string) {
    // 创建一个新的xhr对象
    var xhr = null
    var okStatus = document.location.protocol === 'file' ? 0 : 200
    xhr = window.XMLHttpRequest ? new XMLHttpRequest() : new ActiveXObject('Microsoft.XMLHTTP')
    xhr.open('GET', filePath, false)
    xhr.overrideMimeType('text/html;charset=utf-8')
    xhr.send(null)
    return xhr.status === okStatus ? xhr.responseText : null
  }
  updateNodeName(...args: any) {
    console.log('updateNodeName', ...args)
  }
  click(...args: any) {
    console.log('click', ...args)
  }
  mounted() {}
}
